import {
  Component,
  ElementRef,
  HostBinding,
  Input,
  OnDestroy,
  OnInit,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularCustomEventType,
  AdaptRadarAngularService,
  AdaptRadarAngularGenericDirective
} from '../common/radar/adapt-radar';
import {MenuInnerService} from './menu.service';
import {AdaptDeviceDetectionService} from '../common/device-detection.service';
import {MenuConfig, MenuFilterTextsModel, MenuRadarMetadata, MenuTextsModel} from './models/menu-config';
import {CascadeItem, MenuItem, MenuItemFilteringConfig} from './models/menu-item';
import {MenuGlobalService} from './menu-global.service';
import {AdaptTranslateService} from '../common/i18n/index';
import {KeyValueObject} from '../common/common.models';

let count = 0;


@Component({
  selector: 'adapt-menu',
  templateUrl: './menu.component.html',
  providers: [MenuInnerService],
  encapsulation: ViewEncapsulation.None,
})
export class AdaptMenuComponent extends AdaptRadarAngularGenericDirective implements OnInit, OnDestroy {
  /**
   * Menu item to build nested cascade
   * @docs-default
   */
  @Input() cascadeItem: MenuItem;

  /**
   * @docs-default
   */
  @Input() filteringConfig: MenuItemFilteringConfig;

  /**
   * When specified, previously opened submenu will be automatically closed after new submenu opened
   * @docs-default true
   */
  @Input() closePrevious: boolean = true;

  /**
   * @docs-default
   */
  @Input() analyticsMetadata: MenuRadarMetadata = null;

  /**
   * Set color theme for root menu item. Supported values: 'dark', 'grey', 'white'
   * @docs-default dark
   */
  @Input()
  set headerTheme(v: string) {
    switch (v.toLowerCase()) {
      case 'grey':
        this._headerTheme = 'grey';
        break;
      case 'white':
        this._headerTheme = 'white';
        break;
      case 'dark':
        this._headerTheme = 'dark';
        break;
      case '':
        this._headerTheme = 'dark';
        break;
      default:
          this._headerTheme = 'custom';
    }
  }

  get headerTheme(): string {
    return this._headerTheme;
  }

  /**
   * Set color theme for submenu items. Supported values: 'dark', 'grey', 'white'
   * @docs-default dark
   */
  @Input()
  set bodyTheme(v: string) {
    switch (v.toLowerCase()) {
      case 'grey':
        this._bodyTheme = 'grey';
        break;
      case 'white':
        this._bodyTheme = 'white';
        break;
      case 'dark':
        this._bodyTheme = 'dark';
        break;
      case '':
        this._bodyTheme = 'dark';
        break;
      default:
        this._bodyTheme = 'custom';
    }
  }

  get bodyTheme(): string {
    return this._bodyTheme;
  }

  /**
   * Text constant setup for mobile view
   * @docs-default {cancelBtn: 'Cancel', backBtn: 'Back'}
   */
  @Input() mobileViewTexts: MenuTextsModel;

  /**
   * When specified, the submenu items will be attached to the body
   * @docs-default false
   */
  @Input() appendToBody: boolean = false;

  /**
   * @docs-default false
   */
  @Input() connectMenus: boolean = false;

  /**
   * Control possibility to set menu item as active. Can be useful when you don't need to connect menu with your routing
   * @since 10.15.0
   * @docs-default true
   */
  @Input() makeItemActive: boolean = true;

  @HostBinding('class.adapt-menu') defaultHostClass: boolean = true;

  @HostBinding('class.is-mobile') get mobileHostClass(): boolean {
    return this.isMobile;
  }

  @HostBinding('id') id: string;

  private _headerTheme: string = '';
  private _bodyTheme: string = '';
  private _defaultTexts: MenuFilterTextsModel = {};
  private _defaultMobileTexts: MenuTextsModel = {};

  get isMobile(): boolean {
    return this.deviceDetection.deviceMobile();
  }

  get cascade(): CascadeItem {
    return this._menuInnerService.cascadeChain;
  }

  constructor(private _menuInnerService: MenuInnerService,
              private _menuGlobalService: MenuGlobalService,
              private deviceDetection: AdaptDeviceDetectionService,
              private _renderer: Renderer2,
              private ts: AdaptTranslateService,
              protected _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptMenu);

    this.id = `adapt-menu-${count++}`;
    this.setDefaultTexts(ts.getCurrentLanguage());
  }

  ngOnInit(): void {
   this.mobileViewTexts = {...this._defaultMobileTexts, ...this.mobileViewTexts};
    const config = this as MenuConfig;

    this._menuInnerService.init(config);

    if (this.connectMenus) {
      this._menuGlobalService.connectNodes(this.cascadeItem);
    }

    this.filteringConfig = {...this._defaultTexts, ...this.filteringConfig};
  }

  ngOnDestroy(): void {
    if (this.connectMenus) {
      this._menuGlobalService.destroyConnectNodes();
    }
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this._defaultTexts.placeholder = translation['adapt.common.filter'];
    this._defaultTexts.resultsTitle = translation['adapt.menu.resultsTitle'];
    this._defaultTexts.title = translation['adapt.menu.title'];
    this._defaultTexts.notFoundText = translation['adapt.menu.notFoundText'];
    this._defaultTexts.btnClearText = translation['adapt.common.clear'];
    this._defaultMobileTexts.backBtn = translation['adapt.common.back'];
    this._defaultMobileTexts.cancelBtn = translation['adapt.common.cancel'];
  }

  selectMenuItem(): void {
    if (this.makeItemActive) {
      this._menuGlobalService.selectMenuItem(this.cascadeItem);
    }
  }

  getCascadeLength(): number {
    return this._menuInnerService.getCascadeLength();
  }

  handleMenuRootItemClick(item: MenuItem): void {
    /* TODO: Do we need to send event here? */
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ParentComponent: this.analyticsMetadata?.parentComponentName,
      Name: item.name || item.label,
      Disabled: item.disabled,
      Type: 'menu',
      SiblingsOptionsCount: this.analyticsMetadata?.siblingsCount,
      HiddenSiblingsOptionsCount: this.analyticsMetadata?.hiddenSiblingsCount
    });
  }

  handleMenuLinkClick(item: MenuItem): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ParentComponent: this.analyticsMetadata?.parentComponentName,
      Name: item.name || item.label,
      Disabled: item.disabled,
      Type: 'link',
      SiblingsOptionsCount: this.analyticsMetadata?.siblingsCount,
      HiddenSiblingsOptionsCount: this.analyticsMetadata?.hiddenSiblingsCount
    });
  }

  handleMenuRouteClick(item: MenuItem): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ParentComponent: this.analyticsMetadata?.parentComponentName,
      Name: item.name || item.label,
      Disabled: item.disabled,
      Type: 'route',
      SiblingsOptionsCount: this.analyticsMetadata?.siblingsCount,
      HiddenSiblingsOptionsCount: this.analyticsMetadata?.hiddenSiblingsCount
    });

    this.selectMenuItem();
  }

  handleMenuActionClick(event: Event, item: MenuItem): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ParentComponent: this.analyticsMetadata?.parentComponentName,
      Name: item.name || item.label,
      Disabled: item.disabled,
      Type: 'action',
      SiblingsOptionsCount: this.analyticsMetadata?.siblingsCount,
      HiddenSiblingsOptionsCount: this.analyticsMetadata?.hiddenSiblingsCount
    });

    this.selectMenuItem();

    item.action(event, item.id);
  }

  generateMenuItemTestID(item: MenuItem): string {
    if (!item.testID) {
      item.testID = `menu-item-test-id-${count}`;
    }

    return item.testID;
  }
}
